			console.log(data)
			const res = await this.$http.get('/data/company',{data: data})
			console.log(res)
			this.content6 = res.data.data
			
		},
		
		async getRegionCode(){
			const res = await this.$http.get('/choose/region',{})
			console.log(res)
			let tmp = res.data.data.list
			tmp.forEach((item)=>{
				this.regionCodeList0.push({label:item.label,id:item.id})
				this.regionCodeList1.push({label:item.label,id:item.id})
				this.regionCodeList2.push({label:item.label,id:item.id,buyOrSell: item.buyOrSell})
				this.regionCodeList3.push({label:item.label,id:item.id})
				this.regionCodeList4.push({label:item.label,id:item.id})
				this.regionCodeList5.push({label:item.label,id:item.id})
				this.regionCodeList6.push({label:item.label,id:item.id})
				
			})
			
			// this.regionCodeList1.unshift({label:'全部',id: 0})
			this.regionCodeList1.unshift({label:'全部',id: 0})
			this.regionCodeList2.unshift({label:'全部',id: 0,buyOrSell:0})
			this.regionCodeList3.unshift({label:'全部',id: 0})
			this.regionCodeList4.unshift({label:'全部',id: 0})
			this.regionCodeList5.unshift({label:'全部',id: 0})
			this.regionCodeList6.unshift({label:'全部',id: 0})
			
		},
		filterInput:function(type){
			if (type ==5 ){
				this.fetchList5()
			}
			else if (type ==4){
				this.fetchList4()
			}else if (type ==3){
				this.fetchList3()
				
			}else if (type==2){
				this.fetchList2()
			}else if (type==1){
				this.fetchList1()
			}
			
		},
		tabSwitchRegion:function(type,id,buyOrSell){
		
			switch(type){
				case 0:
					this.regionCode0 = id
					this.fetchList0()
					break
				case 1:
					this.regionCode1 = id
					this.fetchList1()
					break
				case 2:
					this.regionCode2 = id
					this.userPostCode2 = buyOrSell
					this.fetchList2()
					break
				case 3:
					this.regionCode3 = id
					this.fetchList3()
					break
				case 4:
					this.regionCode4 = id
					this.fetchList4()
					break
				case 5:
					this.regionCode5 = id
					this.fetchList5()
					break
				case 6:
					this.regionCode6 = id
					this.fetchList6()
					break
				
			}
		},
		toSetTargetCustomer:function(){
			if (!this.isShowFilter){
				uni.navigateTo({
					url: `./setTargetCustomer`
				});
			}
			
		
		},
		toSetTargetMoney:function(){
			
				if (parseInt(this.today) < 7){
					uni.navigateTo({
						url: `./targetCustomList`
					});
				}else {
					uni.showToast({
						title: '超过设定时间',
						icon: 'none',
						duration: 1000
					});
				}
			
			
		},
		toSelectTime:function(index){
			if (index == 0){
			   uni.navigateTo({
			   	url: `./selectMonths?type=${index}`
			   });	
			   return
			}
			if (index == 1){
			   uni.navigateTo({
			   	url: `./selectMonths?type=${index}`
			   });	
			   return
			}
			uni.navigateTo({
				url: `./selectTime?type=${index}`
			});
		},
		tabSwitch2:function(index){
			// this.activeIndex2 = index
			// this.content2Detail = this.content2.list[this.activeIndex2]
			this.deputyCode = index
			this.fetchList0()
		},
		tabSwitch:function(index){
			this.activeIndex = index
		    
			switch (index){
					case 0:
					    this.selectorQuery("#outView","#tabbar0");
						
						break;
					case 1:
					    this.selectorQuery("#outView","#tabbar1");
						
						break;
					case 2:
					    this.selectorQuery("#outView","#tabbar2");
						
						break;
					case 3:
					   this.selectorQuery("#outView","#tabbar3");
					   
					   break;
					case 4:
					   this.selectorQuery("#outView","#tabbar4");
					  
					   break;
					case 5:
					   this.selectorQuery("#outView","#tabbar5");
					  
					   break;
					case 6:
					   this.selectorQuery("#outView","#tabbar6");
					  
					   break;
				}
			
		},
		selectorQuery:function(outView,currentView){
			uni.createSelectorQuery().select(outView).boundingClientRect(data=>{
			    uni.createSelectorQuery().select(currentView).boundingClientRect((res)=>{
						uni.pageScrollTo({
						    duration:0,
						    scrollTop:res.top - data.top 
						})
				    }).exec()
			}).exec();
		},
	}

}
